.paymentPage {
    background: #ffffff;
    border-radius: 10px;
    padding: 15px;
    margin: 25px 0;
}
.paymentPage-img {
    width: 25%;
    max-width: 100%;
    float: left;
    margin: 10px 10px;
}
.paymentPage-title {
    color: #000;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin: 5px 0;
}
.paymentPage-description {
    font-size: 18px;
}
