.product-list-item {
    color:white;
    border:1px solid white;
    padding:15px;
    margin: 10px;
    background: rgba(51,102,204,0.75);
    font-size: 21px;
    letter-spacing: 0.5px;
    
}
.product-title {
    font-size: 26px;
    margin-bottom: 15px;
    font-weight: bold;
}
.product-description {
    font-weight: 300;
    margin-bottom: 15px;
}
.product-features {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 15px;
}
.btn-add-to-cart {
    text-transform: uppercase;
    font-size: 21px;
    background: #3366cc;
    width: 215px;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
    display: block;
    color:white;
    border: 1px solid white;
}
.btn-add-to-cart:hover {
    cursor: pointer;
    background: #6699cc;
}
.product-price {
    font-size: 36px;
    text-align: center;
    margin: 10px 0;
}
.product-image {
    background-color: rgba(255,255,255,0.75);
    padding: 15px 0;
    text-align: center;
    margin-bottom: 10px;
    position: relative;
}
.product-quantity button {
    width: 30px;
    height: 30px;
    background-color: grey;
    color: #fff;
    border: none;
    cursor: pointer;
    font-size: 14px;
}
.product-quantity button:hover {
    border: 1px solid #ccc;
    background-color: rgb(0, 217, 255);
    color: red;
}
.product-quantity {
    text-align: center;
}
.product-quantity input {
    height: 30px;
    font-size: 14px;
    text-align: center;
    outline: none;
    border: none;
}
.product-quantity button:disabled {
    background: red;
    opacity: 0.8;
    transition: 0.2s;
}
.product-quantity button:disabled:hover {
    color: #fff;
}
.like {
    position: absolute;
    top: 5px;
    right: 5px;
    color: #fff;
    font-size: 21px;
    border: none;
    background: transparent;
    outline: none;
}
.like-active, .like:hover {
    color: red;
}