body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
	box-sizing: border-box;
}
body {
	background-image: url(data:image/png;base64,);
}
.container {
	
	margin:0 auto;
}
.row:after {
	display: block;
	clear: both;
	content:"";
}
[class*="col-"] {
	float: left;
}
.col-lg-3 {
	width: 25%;
}
.col-lg-6 {
	width: 50%;
}
.col-lg-9 {
	width: 75%;
}
.text-center {
	text-align: center;
}

@media(min-width: 768px) {
	.container {
		width: 750px;
	}
}
@media(min-width: 992px) {
	.container {
		width: 970px;
	}
}
@media(min-width: 1200px) {
	.container {
		width: 1170px;
	}
}
.header {
	background-color: rgba(255,255,255,0.75);
	padding: 25px 0;
}
.menu ul:after {
	clear: both;
	content: "";
	display: block;
}
.menu > ul > li {
	float: left;
	width: 25%;
	border-right: 1px solid #3366cc;

}
.menu > ul > li:first-child {
	border-left: 1px solid #3366cc;
}
.menu > ul li > a {
	display: block;
	height: 50px;
	text-align: center;
	line-height: 50px;
	text-decoration: none;
	font-size: 21px;
	color:#3366cc;
}
.menu > ul li > a:hover {
	background-color: #3366cc;
	color:white;
	cursor: pointer;
}
.products-price,.products-count {
	color:white;
	font-size: 17px;
	margin:5px 0;
}
h1 {
    text-align: center;
    color: #ffffff;
    margin: 20px 0;
    font-size: 30px;
}
.product-list-item {
    color:white;
    border:1px solid white;
    padding:15px;
    margin: 10px;
    background: rgba(51,102,204,0.75);
    font-size: 21px;
    letter-spacing: 0.5px;
    
}
.product-title {
    font-size: 26px;
    margin-bottom: 15px;
    font-weight: bold;
}
.product-description {
    font-weight: 300;
    margin-bottom: 15px;
}
.product-features {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 15px;
}
.btn-add-to-cart {
    text-transform: uppercase;
    font-size: 21px;
    background: #3366cc;
    width: 215px;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
    display: block;
    color:white;
    border: 1px solid white;
}
.btn-add-to-cart:hover {
    cursor: pointer;
    background: #6699cc;
}
.product-price {
    font-size: 36px;
    text-align: center;
    margin: 10px 0;
}
.product-image {
    background-color: rgba(255,255,255,0.75);
    padding: 15px 0;
    text-align: center;
    margin-bottom: 10px;
    position: relative;
}
.product-quantity button {
    width: 30px;
    height: 30px;
    background-color: grey;
    color: #fff;
    border: none;
    cursor: pointer;
    font-size: 14px;
}
.product-quantity button:hover {
    border: 1px solid #ccc;
    background-color: rgb(0, 217, 255);
    color: red;
}
.product-quantity {
    text-align: center;
}
.product-quantity input {
    height: 30px;
    font-size: 14px;
    text-align: center;
    outline: none;
    border: none;
}
.product-quantity button:disabled {
    background: red;
    opacity: 0.8;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
.product-quantity button:disabled:hover {
    color: #fff;
}
.like {
    position: absolute;
    top: 5px;
    right: 5px;
    color: #fff;
    font-size: 21px;
    border: none;
    background: transparent;
    outline: none;
}
.like-active, .like:hover {
    color: red;
}
.cartPage-priceForOne {
    margin: 0 5px;
    color: red;
}
.cartPage {
    font-size: 25px;
}
.cart-product-list-item-description {
    margin: 25px 0;
    padding: 15px;
    border: 1px dotted;
    border-radius: 10px;
    color: black;
    position: relative;
}
.cart-product-list-item-description img {
    display: block;
    margin: 0 auto;
}
.product-image {
    position: relative;
}
.cart-extended-name {
    font-size: 20px;
    text-align: center;
    margin-bottom: 15px;
}
.cart-extended-price {
 font-size: 13px;
 font-style: italic;
 margin-bottom: 10px;
}
.bold {
    font-weight: bold;
}
.cart-extended-sum {
    font-size: 16px;
    text-align: right;
}
.sum-price {
    font-size: 20px;
    color: #3366cc;
}
.cart-product-list-item-description {
    background: white;
}
.cart-extended_delete {
    position: absolute;
    right: 2%;
    padding: 2px 5px;
    background: red;
    border: 0;
    text-align: right;
    color: white;
    font-size: 12px;
}
.paymentPage {
    background: #ffffff;
    border-radius: 10px;
    padding: 15px;
    margin: 25px 0;
}
.paymentPage-img {
    width: 25%;
    max-width: 100%;
    float: left;
    margin: 10px 10px;
}
.paymentPage-title {
    color: #000;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin: 5px 0;
}
.paymentPage-description {
    font-size: 18px;
}

.shippingPage {
    background: #fff;
    border-radius: 10px;
    padding: 15px;
    margin: 25px 0;
}
.shippingPage-img {
    width: 25%;
    float: left;
    margin: 10px 10px;
}
.shippingPage-title {
    font-size: 24px;
    text-align: center;
    font-weight: bold;
    margin: 5px 0;
    color: #000;
}
.shippingPage-description {
    font-size: 18px;
}
.content-height {
    height: 175px;
}
