.shippingPage {
    background: #fff;
    border-radius: 10px;
    padding: 15px;
    margin: 25px 0;
}
.shippingPage-img {
    width: 25%;
    float: left;
    margin: 10px 10px;
}
.shippingPage-title {
    font-size: 24px;
    text-align: center;
    font-weight: bold;
    margin: 5px 0;
    color: #000;
}
.shippingPage-description {
    font-size: 18px;
}
.content-height {
    height: 175px;
}